.orderAccount {
  margin-top: 20px;
  border: 2px solid rgb(180, 180, 180);
  background-color: #e4e4e4;
  padding: 20px;
  width: 100%;
}
.orderAccount span {
  font-size: 16px;
  font-weight: bold;
}

.orderAccount select {
  width: 400px;
  padding: 5px;
  font-size: 16px;
  font-family: inherit;
  border-radius: 5px;
  outline: none;
  margin: 0 20px;
}

@media (max-width: 1020px) {
  .orderAccount span,
  .orderAccount select {
    font-size: 15px;
  }
}

@media (max-width: 750px) {
  .orderAccount span,
  .orderAccount select {
    font-size: 14px;
    width: 300px;
  }
}
